import { combineReducers } from "redux";
import { StandardAction } from "@/src/redux/Types";

// Error reducer
const error = (state = null, action: StandardAction) => {
  switch (action.type) {
    case "SET_ERROR":
      return action.payload;
    default:
      return state;
  }
};

// Combine reducers
const reducers = combineReducers({
  error,
});

export default reducers;
