import { useMemo } from "react";
import { Environment, Network, RecordSource, Store } from "relay-runtime";

// Create a singleton environment
let relayEnvironment: Environment | null = null;

// fetchQuery
//
// A function that fetches the results of an operation (query/mutation/etc)
// and returns its results as a Promise:
// https://relay.dev/docs/en/experimental/api-reference#fetchquery
const fetchQuery = async (
  operation: any,
  variables: any,
  cacheConfig: any,
  uploadables: any
): Promise<any> => {
  const response = await fetch(
    "https://nftxledger-worker-dev.aderbal.workers.dev",
    {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
      body: JSON.stringify({
        query: operation.text,
        variables,
      }),
    }
  );

  return await response.json();
};

// createEnvironment
//
// Creates and configures a Relay environment:
const createEnvironment = () => {
  return new Environment({
    network: Network.create(fetchQuery),
    store: new Store(new RecordSource()),
  });
};

//  initializeEnvironment
//
//  Initializes a Relay environment. Creates a new Relay environment if one
//  does not already exist. Otherwise, reuses the existing environment.
const initializeEnvironment = () => {
  // Create a network layer from the fetch function.
  const environment = relayEnvironment ?? createEnvironment();

  // For SSG and SSR always create a new Relay environment
  if (typeof window === "undefined") return environment;
  // Create the Relay environment once in the client
  if (!relayEnvironment) relayEnvironment = environment;

  return relayEnvironment;
};

// useEnvironment
//
// A custom React hook that re-creates the Relay environment on every call.
// This is useful when you want to ensure that Relay re-fetches data from the
// server on every client-side navigation.
// https://relay.dev/docs/en/experimental/a-pre-relay-hooks-integration
// https://relay.dev/docs/en/experimental/api-reference#usepreloadedquery
export function useEnvironment() {
  const store = useMemo(() => initializeEnvironment(), []);
  return store;
}
